.colors-type__type{
  max-width: 466px;
  color: rgba(31, 32, 65);
  // margin-right: 10px;

  &-sample {
    width: 100%;
    @include index.isFlex(row, flex-start, flex-start, nowrap);
    margin: 20px 0 50px 0;
    padding-right: 20px;
  }

  &-tag {
    min-width: 40px;
    margin-right: 30px;
    color: index.$dark-shade25;
    text-align: right;
  }

  &-text {
    @extend %fontBold;
  }
}

.colors-type__type div:nth-child(1) {
  div:nth-child(1) {
    font-size: 24px;
  }

  div:nth-child(2) {
    font-size: 24px;
  }
}

.colors-type__type div:nth-child(2) {
  div:nth-child(1) {
    font-size: 19px;
  }

  div:nth-child(2) {
    font-size: 19px;
  }
}

.colors-type__type div:nth-child(3) {
  div:nth-child(1) {
    font-size: 12px;
  }

  div:nth-child(2) {
    font-size: 12px;
    text-transform: uppercase;
  }
}

.colors-type__type div:nth-child(4) {
  div:nth-child(1) {
    font-size: 14px;
  }

  div:nth-child(2) {
    line-height: 24px;
    font-size: 14px;
    @extend %bodyText;
    padding-right: 60px;

  }
}
